:root {
    --ferbend-advertisement-bg: linear-gradient(180deg, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.7));
}

[data-bs-theme=dark] {
    --ferbend-advertisement-bg: linear-gradient(180deg, rgba(33, 37, 41, 0.7), rgba(0, 0, 0, 0.7))!important;
}


.advertisement {
    transition: all 1s ease-in-out;
    border-radius: var(--ferbend-border-radius-xl);
    overflow: hidden;
}

.advertisement .maintenance-label {
    color: var(--ferbend-text);
    font-size: .8rem;
}

.advertisement .card-footer {
    background: transparent !important;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
    padding-top: 1rem;
}

.card-cover {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}

.feature-icon-small {
    width: 3rem;
    height: 3rem;
}

.card-footer .feature-icon-small {
    height: 1rem;
    width: 1rem;
}

.carousel-item .picture-container {
    height: 15rem;
}

.carousel-item .picture-container .picture {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@keyframes cardZoomIn {
    from {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }
}



.advertisement-container-opened {
    z-index: 10000;
    position: fixed;
    left: 0;
    margin-top: -3rem;
    width: 100%;
    height: calc(100vh) !important;
}
.advertisement-container-opened-full-screen {
    margin-top: 0!important;
}

.advertisement-opened {
    background: transparent;
    background-image: var(--ferbend-advertisement-bg);
    backdrop-filter: blur(1rem);
    border-radius: 0;
    border: none;
}

.advertisement-opened .carousel-item .picture-container {
    height: 100vh;
}

.advertisement-opened .details {
    overflow: auto;
    height: 100vh!important;
    padding-right: 2.25rem!important;
    padding-left: 1.5rem;
    padding-top: 1rem;
    padding-bottom: .75rem;
}

.advertisement-opened .map {
    width: 100%;
    height: 20rem;
    border: none;
    border-radius: var(--ferbend-border-radius-lg);
}

.advertisement .more-details {
    display: none;
}

.advertisement-opened .more-details {
    display: block;
}

.advertisement-opened .copyright-advice {
    display: none;
}

.advertisement-opened .price-container {
    display: none;
}

.advertisement-opened .card-footer {
    display: none!important;
}

.advertisement .btn-close-advertisement-container {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 2000;
}

.advertisement .btn-close-advertisement {
    position: fixed;
    top: 0;
    right: 0;
    border: 0;
    margin: 1rem;
    border-radius: 50%;
    height: 2.6rem;
    width: 2.6rem;
    color: rgb(255,255,255);
    background: rgba(150, 150, 150, .5);
}

.card-feature-icon, .card-feature-title {
    display: inline-block;
    vertical-align: middle;
}

.card-feature-icon {
    height: 1.2rem;
}

.card-feature-title {
    font-size: 1rem;
}

@media (max-width: 576px) {
    .advertisement-container-opened {
        position: unset!important;
    }
    .advertisement-container-opened .advertisement {
        overflow: auto !important;
    }
    .advertisement-container-opened .advertisement-opened .details {
        overflow: unset!important;
    }
    .advertisement-opened .carousel-item .picture-container {
        height: 20rem!important;
    }
    .advertisement-opened .details {
        padding-right: 0!important;
    }
    .advertisement .fake-row {
        margin-left: 0!important;
        margin-right: 0!important;
    }
    .details {
        padding: 0!important;
    }
}