:root {
    --animate-delay: 0.25s;
    --magic-cover-opacity: .7;
}

.hidden {
    display: none;
}

.visible {
    display: block;
}

.magic-cover {
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -100;
    transition: opacity 1s;
    opacity: 0;
}

.magic-cover-active {
    opacity: var(--magic-cover-opacity)!important;
}

.magic-cover .picture {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: blur(4rem) brightness(1.5);
}

@-webkit-keyframes mcFadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: var(--magic-cover-opacity);
    }
}

@keyframes mcFadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: var(--magic-cover-opacity);
    }
}

.animate__mcFadeIn {
    -webkit-animation-name: mcFadeIn;
    animation-name: mcFadeIn;
}

@-webkit-keyframes mcFadeOut {
    from {
        opacity: var(--magic-cover-opacity);
    }

    to {
        opacity: 0;
    }
}

@keyframes mcFadeOut {
    from {
        opacity: var(--magic-cover-opacity);
    }

    to {
        opacity: 0;
    }
}

.animate__mcFadeOut {
    -webkit-animation-name: mcFadeOut;
    animation-name: mcFadeOut;
}

.vertical-align-parent {
    display: table;
    width: 100%;
    height: 100%;
}

.vertical-align-child {
    display: table-cell;
    vertical-align: middle;
}

.btn-spinner {
    margin-left: 1rem;
}

.text-shadow-light {
    text-shadow: 0 0 1.5rem var(--ferbend-light);
}

.text-shadow-dark {
    text-shadow: 0 0 1.5rem var(--ferbend-dark);
}

[data-bs-theme=dark] input {
    color-scheme: dark !important;
}

.gap {
    gap: 1rem;
}

