.form-signin-container {
    height: 100vh!important;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1500;
    backdrop-filter: blur(var(--ferbend-gblur-radius));
    background: var(--ferbend-form-control-bg);
}
.form-signin {
    max-width: 350px;
    padding: 15px;
}

.form-signin-logo {
    margin-left: auto;
    margin-right: auto;
    display: block!important;
    height: 3rem;
    width: 3rem;
}

.form-top-close-btn {
    position: absolute;
}