@keyframes loading-spinner {
    0% {
        opacity: 1
    }

    100% {
        opacity: 0
    }
}

.loading-spinner div {
    left: 48px;
    top: 26px;
    position: absolute;
    animation: loading-spinner linear 0.8s infinite;
    background: #fe718d;
    width: 4px;
    height: 12px;
    border-radius: 0.96px / 0.96px;
    transform-origin: 2px 24px;
}

.loading-spinner div:nth-child(1) {
    transform: rotate(0deg);
    animation-delay: -0.7333333333333333s;
    background: #fe718d;
}

.loading-spinner div:nth-child(2) {
    transform: rotate(30deg);
    animation-delay: -0.6666666666666666s;
    background: #fe718d;
}

.loading-spinner div:nth-child(3) {
    transform: rotate(60deg);
    animation-delay: -0.6s;
    background: #fe718d;
}

.loading-spinner div:nth-child(4) {
    transform: rotate(90deg);
    animation-delay: -0.5333333333333333s;
    background: #fe718d;
}

.loading-spinner div:nth-child(5) {
    transform: rotate(120deg);
    animation-delay: -0.4666666666666667s;
    background: #fe718d;
}

.loading-spinner div:nth-child(6) {
    transform: rotate(150deg);
    animation-delay: -0.4s;
    background: #fe718d;
}

.loading-spinner div:nth-child(7) {
    transform: rotate(180deg);
    animation-delay: -0.3333333333333333s;
    background: #fe718d;
}

.loading-spinner div:nth-child(8) {
    transform: rotate(210deg);
    animation-delay: -0.26666666666666666s;
    background: #fe718d;
}

.loading-spinner div:nth-child(9) {
    transform: rotate(240deg);
    animation-delay: -0.2s;
    background: #fe718d;
}

.loading-spinner div:nth-child(10) {
    transform: rotate(270deg);
    animation-delay: -0.13333333333333333s;
    background: #fe718d;
}

.loading-spinner div:nth-child(11) {
    transform: rotate(300deg);
    animation-delay: -0.06666666666666667s;
    background: #fe718d;
}

.loading-spinner div:nth-child(12) {
    transform: rotate(330deg);
    animation-delay: 0s;
    background: #fe718d;
}

.loading-spinner-container {
    width: 80px;
    height: 80px;
    display: block;
    overflow: hidden;
    margin-left: auto;
    margin-right: auto;
}

.loading-spinner {
    width: 100%;
    height: 100%;
    position: relative;
    transform: translateZ(0) scale(0.8);
    backface-visibility: hidden;
    transform-origin: 0 0;
    /* see note above */
}

.loading-spinner div {
    box-sizing: content-box;
}