.search-box {
    border: var(--ferbend-border-width) solid var(--ferbend-primary);
}

.dropdown-menu li {
    margin-bottom: .5rem;
}

.dropdown-menu li:last-child {
    margin-bottom: 0;
}

.dropdown-menu li a:hover {
    background-color: var(--ferbend-secondary-bg);
    background-image: var(--ferbend-gradient);
    color: var(--ferbend-dropdown-link-color);
}

.dropdown-menu li a.active:hover {
    background-color: var(--ferbend-dropdown-link-active-bg);
    background-image: var(--ferbend-gradient);
    color: var(--ferbend-dropdown-link-active-color);
}

.navbar .dropdown-menu.w-10 {
    min-width: 10rem;
}

.navbar .dropdown-menu.w-15 {
    min-width: 15rem;
}

.navbar .dropdown-menu.w-20 {
    min-width: 20rem;
}

.navbar .dropdown-menu.w-30 {
    min-width: 30rem;
}

.navbar .dropdown-menu .btn-group {
    width: 20rem;
}

.header-container {
    backdrop-filter: blur(var(--ferbend-gblur-radius));
    box-shadow: 0 0 1.5rem var(--ferbend-border-color-translucent);
    height: 4rem;
}

.header {
    height: 4rem;
}

.header-container-expanded {
    height: 100%;
}

.app-logo {
    height: 2rem;
    width: 2rem;
    display: inline-block;
}

.app-logo,
[data-bs-theme=light] .app-logo {
    background: url('../../../assets/brand/ferbend-logo-colorful.svg') no-repeat;
}

[data-bs-theme=dark] .app-logo {
    background: url('../../../assets/brand/ferbend-logo-white.svg') no-repeat;
}

[data-bs-theme=light] .nav-link {
    text-shadow: 0 0 .5rem white
}

[data-bs-theme=dark] .header-container {
    background: rgba(33, 37, 41, .5);
}

[data-bs-theme=light] .header-container {
    background: rgba(255, 255, 255, .5);
}

@media (max-width: 768px) {
    .navbar-brand {
        margin-right: 0;
    }

    .navbar .dropdown-menu {
        position: static !important;
    }

    .navbar-nav>.nav-item .btn {
        width: 100%;
    }

    .search-box {
        width: calc(100% - 8rem);
    }

    .navbar .dropdown-menu {
        background: transparent !important;
        background-image: var(--ferbend-gradient) !important;
    }

    .navbar-nav {
        margin-top: 1rem;
        margin-bottom: 1rem;
    }

    #appNavbar .btn-register {
        margin-left: 0 !important;
    }
}

.btn-options {
    margin-left: 1rem;
}

.btn-options::after {
    display: none;
}

.dropdown-menu-options {
    right: 0;
    left: unset!important;
}

.navbar-toggler {
    border: none!important;
}