.magic-btn {
    position: relative;
    overflow: hidden;
}

.magic-btn::before {
    content: '';
    position: absolute;
    top: var(--y, 0);
    left: var(--x, 0);
    width: .7rem;
    height: .7rem;
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    background-color: rgba(255, 255, 255, 0.18);
    box-shadow: 0 0 1rem rgba(255, 255, 255);
    pointer-events: none;
    transition: transform 0.5s ease-out;
}

.magic-btn:hover::before {
    transform: translate(-50%, -50%) scale(2);
}

.magic-btn:focus {
    outline: none;
}

.magic-btn:focus::before {
    transform: translate(-50%, -50%) scale(2);
}