.btn-map-zoom {
    height: 36px;
    width: 36px;
    padding: 0;
}

.map {
    width: 100%;
    height: 100%;
    border: none;
}
.map div:first-child {
    background-color: transparent!important;
}

.summary-card-container {
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 100;
    width: 30rem;
}

@media (max-width: 576px) {
    .summary-card-container {
        width: 100%;
    }
}
.summary-card-container .advertisement {
    background-image: var(--ferbend-gradient);
    backdrop-filter: blur(var(--ferbend-gblur-radius));
}

.summary-card-container .advertisement-container-opened {
    margin-top: 0!important;
}
[data-bs-theme=dark] .summary-card-container .advertisement {
    background-color: rgba(33, 37, 41, .5)!important;
    backdrop-filter: blur(var(--ferbend-gblur-radius));
}

[data-bs-theme=light] .summary-card-container .advertisement {
    background-color: rgba(255, 255, 255, .5)!important;
}

.summary-card-container .btn-close-summary-container {
    position: absolute;
    right: 1rem;
    margin-top: 1rem;
    margin-right: 1rem;
    z-index: 10;
}
.summary-card-container .btn-close-summary {
    border: 0;
    border-radius: 50%;
    height: 2.6rem;
    width: 2.6rem;
    color: rgb(255,255,255);
    background: rgba(150, 150, 150, .5);
    backdrop-filter: blur(var(--ferbend-gblur-radius));
}

.gmnoprint {
    display: none!important;
}

.gm-style-cc {
    display: none!important;
}